import {Component, OnInit} from "@angular/core";
import {NavController, NavParams} from "ionic-angular";
import {Video} from "../../models/video";
import {ServiceProvider} from "../../providers/service/service";
import {Api} from "../../environments/api";

/**
 * Generated class for the VideoPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-video',
  templateUrl: 'video.html',
})
export class VideoPage implements OnInit {
  videoId: string;
  errorMessage: string;
  video: Video;

  constructor(public navCtrl: NavController, public navParams: NavParams, private httpService: ServiceProvider) {
    console.log(this.navParams.data);
    this.videoId = this.navParams.data;
    this.video = new Video();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad VideoPage');
  }

  ngOnInit(): void {
    console.log('ngOnInit VideoPage');
    this.httpService.get<Video>(Api.getVideoUrl()).subscribe(
      video => {
        console.log("subscribe")
        this.video = video;
        // Todo 测试用代码
        this.video.url = this.videoId;
        var videoPlayer = document.getElementById("video-player");
        videoPlayer.setAttribute('src', this.video.url);
      },
      error => {
        this.errorMessage = <any>error;
      }
    );
  }

  gotoDownloadPage() {
    console.log("gotoDownloadPage");
  }
}



