import {environment} from "./environment";
/**
 * Created by tangjy on 2017/10/14.
 */
export class Api {
  public static getVideoUrl() {
    return (environment.production) ? "https://easy-mock.com/mock/59cf1534c5c4302238f614c5/ionic-education/video" : "/assets/mock/video.json";
  }

  public static getPreviewUrl() {
    return (environment.production) ? "https://easy-mock.com/mock/59cf1534c5c4302238f614c5/ionic-education/preview" : "/assets/mock/preview.json";
  }
}



