import {Component} from "@angular/core";
import {App, NavController} from "ionic-angular";
import {VideoPage} from "../video/video";
import {PreviewPage} from "../preview/preview";

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})

export class HomePage {
  videoParams = "http://baobab.wdjcdn.com/14564977406580.mp4";
  previewParams = "https://raw.githubusercontent.com/aorise-org/products/master/assets/login.docx";

  constructor(private app: App, public navCtrl: NavController) {

  }

  gotoVideoPage() {
    this.entryVideoPage(this.videoParams);
  }

  private entryVideoPage(params: string) {
    this.app.getRootNav().push(VideoPage, params);
  }

  gotoPreviewPage() {
    this.entryPreviewPage(this.previewParams);
  }

  private entryPreviewPage(params: string) {
    this.app.getRootNav().push(PreviewPage, params);
  }
}



